import React, { Component } from 'react';
import './App.css';
import { Route, Switch, Link } from 'react-router-dom';
import JoinCryptocurrencies from './JoinCryptocurrencies';
import FindWords from './FindWords';
import Quiz from './Quiz';
import logo from './assets/logo.svg';
import style from './css/app.module.css'


class App extends Component {
	constructor(props) {
		super(props);
		this.state = {};
	}

	render() {
		return (
			<div>
				<div className={style.logo}><Link to="/"><img src={logo} alt=""/></Link></div>
				<Switch>
					<Route exact path="/" component={Meniu}//%PUBLIC_URL%
						/>
					<Route path="/join" component={JoinCryptocurrencies} />
					<Route path="/find" component={FindWords} />
					<Route path="/quiz" component={Quiz} />
				</Switch>
			</div>
		);
	}
}

function Meniu() {
	return (
		<div className={style.buttons}>

			<Link to="/join">
				<button className={style.button}>Find a match</button>
			</Link>
			<Link to="/quiz">
				<button className={style.button}>Questions</button>
			</Link>
			<Link to="/find">
				<button className={style.button}>Find a word</button>
			</Link>
		</div>
	);
}

export default App;
