const WinScore = playAgain => (
	<div>
		<div className={style.score}>
			<div className={style.scoreText}>Score</div>
			<div>18/18</div>
		</div>

		<Link to="/">
			<button className={style.back}>Back to games</button>
		</Link>
		<button className={style.retry} onClick={playAgain}>
			Retry
		</button>
	</div>
);

const LoseScore = ({ score, playAgain }) => (
	<div>
		<div className={style.score}>
			<div className={style.scoreText}>Score</div>
			<div>
				{score}
				/18
			</div>
		</div>

		<Link to="/">
			<button className={style.back}>Back to games</button>
		</Link>
		<button className={style.retry} onClick={playAgain}>
			Retry
		</button>
	</div>
);

const Countdown = props => {
	const d = new Date(props.remaining);
	const { minutes, seconds } = {
		minutes: d.getUTCMinutes(),
		seconds: d.getUTCSeconds()
	};
	return <p>{`${minutes}:${seconds.toString().padStart(2, 0)}`}</p>;
};

class Time extends Component {
	constructor(props) {
		super(props);
		this.state = {
			remaining: 300000
		};
	}

	componentDidMount() {
		setInterval(() => this.decreseTime(), 1000);
	}

	decreseTime = () => {
		this.setState(prevState => ({ remaining: prevState.remaining - 1000 }));
	};

	render() {
		return (
			<div className={style.clockContainer}>
				<Countdown remaining={this.state.remaining} />
			</div>
		);
	}
}

